import React, { useState, useCallback, useEffect } from "react";
import { Upload, Modal, Avatar } from "antd";
import ImgCrop from "antd-img-crop";
import { useDropzone } from "react-dropzone";
import { uploadImageApi } from "../../../api/barbershop";
import useBarbershop from "../../../hooks/useBarbershop";
import { getAccessTokenApi } from "../../../api/auth";
import NoAvatar from "../../../assets/img/png/no-avatar.png";

import "./UploadImagesForm.scss";

export default function UploadImagesForm() {
    const [previewImage, setPreviewImage] = useState(null);
    const [previewVisible, setPreviewVisible] = useState(false);
    const [previewTitle, setPreviewTitle] = useState("");
    const barbershop = useBarbershop();

   const [fileList, setFileList] = useState([]);
   ///const [image, setImage] = useState(null)

  const onChange = ({ fileList: newFileList }) => {
    console.log(newFileList);
    console.log(fileList);
    //  var image ={
    //      name: newFileList.name,
    //      originFileObj:newFileList.originFileObj,
    //      response : "Imagen cargada",
    //      type: newFileList.type,
    //      status:"success",
    //      uid: newFileList.uid,
    //      size:newFileList.size,
    //  }
     setFileList(newFileList);
};


  const onPreview = async (file) => {
    let src = file.url;
    if (!src) {
      src = await new Promise((resolve) => {
        const reader = new FileReader();
        reader.readAsDataURL(file.originFileObj);
        reader.onload = () => resolve(reader.result);
      });
    }

    setPreviewTitle(
      file.name || file.url.substring(file.url.lastIndexOf("/") + 1)
    );
    const image = new Image();
    image.src = src;
    setPreviewImage(image.src);
    setPreviewVisible(!previewVisible);
  };


  return (
    <>
      <ImgCrop rotate>
        <Upload
          action={true}
          listType="picture-card"
          fileList={fileList}
          onChange={onChange}
          onPreview={onPreview}
          beforeUpload={() => true}
        >
          {fileList.length < 10 && "+ Upload"}
        </Upload>
      </ImgCrop>
      <Modal
        visible={previewVisible}
        title={previewTitle}
        footer={null}
        onCancel={() => setPreviewVisible(false)}
      >
        <img alt="" style={{ width: "100%" }} src={previewImage} />
      </Modal>
    </>
  );

};


